export const BarChartOptions_1 = () =>({
    color: ['#1e7fec', '#494964'],
    title: {
        show: true,
        text: '',
        subtext: '',
        y: -7,
        x: 'right',
        subtextStyle: {
            color: '#464654',
            fontWeight: 'bold'
        }
    },
    tooltip: {
        trigger: 'axis',  // axisitem
        showDelay: 0,   //tips
        hideDelay: 1000, //tips
        transitionDuration: 0,  //tips
        backgroundColor: 'rgba(244,90,30,1)',
        axisPointer: {  //
            type: 'line',  // 'line' | 'shadow'
            lineStyle: {
                color: '#36364a',
                width: 1,
                type: 'solid'
            }
        },
        formatter: function (params) {
            var res = params[0].name;
            for (var i = 0; i < params.length; i++) {
                res += '<br/>' + params[i].seriesName + ' : ' + params[i].data;
            }
            return res;
        }
    },
    legend: {
        top: 20,
        left: 10,
        itemWidth: 20,
        itemHeight: 15,
        data: [{name: ''}, {name: ''}],
        textStyle: {
            color: '#787878',
            fontWeight: 700
        },
    },
    grid: {
        left: 70,
        right: '4%',
        bottom: '8%',
        containLabel: false
    },
    xAxis: [
        {
            type: 'category',
            show: true,
            boundaryGap: true, //
            axisLabel: {  //X
                textStyle: {
                    color: '#474754',
                    fontWeight: 700
                }
            },
            axisLine: {
                show: false  //X
            },
            splitLine: { //
                show: false
            },
            textStyle: {
                color: ['#FFF']
            },
            axisTick: false,  //
            data: ['1:00', '5:00', '13:00', '17:00', '21:00', '22:00', '23:00'],
        }
    ],
    yAxis: [
        {
            type: 'value',
            axisLine: {
                show: false
            },
            axisTick: {
                show: false
            },
            axisLabel: {
                textStyle: {
                    color: "#565475"
                }
            },
            splitLine: {
                lineStyle: {
                    color: ['#262633']
                }
            },
        }
    ],
    series: [
        {
            name: '',
            type: "bar",
            barGap: 0,
            barWidth: 0,
        },
        {
            name: '',
            type: "bar",
            barGap: 0,
            barWidth: 0,
        },
    ]
});

export const BarChartOption_2 = () =>({
    color: ['#db9713', '#fcc355', '#1d8a8e', '#40cacc', '#b2162c', '#ec1e3b'],
    tooltip: {
        trigger: 'axis',
        backgroundColor: 'rgba(0,0,0,.7)',
        axisPointer: {            // 
            type: 'shadow'        // 'line' | 'shadow'
        }
    },
    legend: {
        top: 10,
        left: 10,
        itemWidth: 10,
        itemHeight: 15,
        textStyle: {
            color: '#787878',
            fontWeight: 700
        },
        data: ['', '', '', '', '', ''],
        selectedMode: false
    },
    grid: {
        left: '3%',
        right: '4%',
        bottom: '3%',
        containLabel: true
    },
    xAxis: [
        {
            type: 'value',
            show: false,
            max:'50%',
            min:'50%'
        }
    ],
    yAxis: [
        {
            type: 'category', //
            axisLine: {
                show: true,  //Y
                lineStyle: {
                    color: '#1e1e28',
                    width: 2,
                    type: 'solid'
                }
            },
            axisLabel: {  //Y
                textStyle: {
                    color: '#565475',
                    fontWeight: 700
                }
            },
            splitLine: {   //
                show: true,
                lineStyle: {
                    color: ['#262633']
                }
            },
            axisTick: {show: false},  //
            data: []
        }
    ],
    series: [
        {
            name: '',
            type: 'bar',
            stack: '',
            label: {
                normal:{
                    show: true,
                    position: 'insideRight',
                    formatter: function (params) {
                        return Math.abs(params.value);
                    }
                }
            }
        },
        {
            name: '',
            type: 'bar',
            stack: '',
            label: {
                normal:{
                    show: false,
                    position: 'insideRight'
                }
            }
        },
        {
            name: '',
            type: 'bar',
            stack: '',
            label: {
                normal:{
                    show: false,
                    position: 'insideLeft'
                }
            }
        },
        {
            name: '',
            type: 'bar',
            stack: '',
            label: {
                normal:{
                    show: true,
                    position: 'insideLeft'
                }
            }
        },
        {
            name: '',
            type: 'bar',
            stack: '',
            label: {
                normal:{
                    show: true,
                    position: 'insideRight',
                    textStyle:{
                        color:'#FFF'
                    }
                }
            }
        },
        {
            name: '',
            type: 'bar',
            stack: '',
            label: {
                normal:{
                    show: true,
                    position: 'insideLeft',
                    textStyle:{
                        color:'#FFF'
                    }
                }
            }
        }
    ]
});



