export const LineChartOption_1 = (value?:any) => {
    let option = {
        color: ['#f45a1e', '#294181'],
        title: {
            show: true,
            text: '',
            subtext: '',
            y: -7,
            x: 'right',
            subtextStyle: {
                color: '#464654',
                fontWeight: 'bold'
            }
        },
        tooltip: {
            trigger: 'axis',  // axisitem
            showDelay: 0,   //tips
            hideDelay: 1000, //tips
            transitionDuration: 0,  //tips
            backgroundColor: 'rgba(244,90,30,1)',
            axisPointer: {  //
                type: 'line',  // 'line' | 'shadow'
                lineStyle: {
                    color: '#36364a',
                    width: 1,
                    type: 'solid'
                }
            },
            formatter: function (params) {
                var res = params[0].name;
                for (var i = 0; i < params.length; i++) {
                    res += '<br/>' + params[i].seriesName + ' : ' + params[i].data;
                }
                return res;
            }
        },
        legend: {
            top: 20,
            left: 10,
            data: [{name: '', icon: 'circle'}],
            textStyle: {
                color: '#787878',
                fontWeight: 700
            },
        },
        grid: {
            left: '3%',
            right: '5%',
            bottom: '5%',
            containLabel: true
        },
        xAxis: [
            {
                type: 'category',
                boundaryGap: false,
                axisLine: {
                    show: false,
                },
                axisTick: {
                    show: false
                },
                axisLabel: {
                    textStyle: {
                        color: "#565475"
                    },
                    margin: 16,

                },
                splitLine: {
                    lineStyle: {
                        color: '#333'
                    }
                },
                data: ['1:00', '5:00', '13:00', '17:00', '21:00', '22:00', '23:00'],
            }
        ],
        yAxis: [
            {
                type: 'value',
                axisLine: {
                    show: false
                },
                axisTick: {
                    show: false
                },
                axisLabel: {
                    textStyle: {
                        color: "#565475"
                    },
                    formatter:null
                },
                splitLine: {
                    lineStyle: {
                        color: ['#262633']
                    }
                },
            }
        ],
        series: [
            {
                name: '',
                type: 'line',
                smooth: false,   //
                symbol: 'emptyCircle',   //
                symbolSize: 4,
            }
        ]
    };
    if(!!value) {
        Object.assign(option, value);
    }
    return option;
};
